
.container_sk {
    position:absolute;
    width:100%;
    height:100%;
}
.wraper{
    position:absolute;
    width:20%;
    height:50%;
}
.con1 {
    top:10%;
    left:10%;
}
.con2{
    top:10%;
    left:40%;
}
.con3{
    top:10%;
    left:70%;
}
.con4{
    left:10%;
    top:60%;
}
.con5 {
    top:60%;
    left:40%;
}
.con6{
    top:60%;
    left:70%;
}
.cell4-content{
    position:absolute;
  //left:20%;
  //top:55%;
    display:block;
    width:100%;
    text-align:center;
    float:left;
    position:relative;
    height:100%;
}
.cell4-content span.img{
    height:90px;
    width:100%;
    text-align:center;
    display:block;
}
.cell4-content span.img img{
    max-height:74px;
}
.cell4-content span.title{
    font-size:16px;
    color:#787878;
    line-height:32px;
}
.cell4-content:hover .cell-detail{
    display:block;
}
.cell-detail p{
		margin-top:12%;
}
.cell-detail{
    width:100%;
    height:160px;
    position:absolute;
  //top:50%;
    margin-top:-160px;
    left:-1px;
    border:1px solid #bad6f5;
    font-size:1.7vw;
    //font-size:14px;
    -webkit-border-radius:10px;
    -moz-border-radius:10px;
    border-radius:10px;
    background:#fff;
    z-index:3;
    text-align:center;
    animation:flipInX 0.7s  ease both;
    -webkit-animation:flipInX 0.7s  ease both;
    -moz-animation:flipInX 0.7s  ease both;
    display:none;
}
@-webkit-keyframes flipInX{
0%{-webkit-transform:perspective(400px) rotateX(90deg);opacity:0.5;
}
40%{-webkit-transform:perspective(400px) rotateX(-10deg)
}
70%{-webkit-transform:perspective(400px) rotateX(10deg)
}
100%{-webkit-transform:perspective(400px) rotateX(0deg);opacity:1.0
}
}
@-moz-keyframes flipInX{
0%{-moz-transform:perspective(400px) rotateX(90deg);opacity:0.5;
}
40%{-moz-transform:perspective(400px) rotateX(-10deg)
}
70%{-moz-transform:perspective(400px) rotateX(10deg)
}
100%{-moz-transform:perspective(400px) rotateX(0deg);opacity:1.0
}
}
@keyframes flipInX{
0%{transform:perspective(400px) rotateX(90deg);opacity:0.5;
}
40%{transform:perspective(400px) rotateX(-10deg)
}
70%{transform:perspective(400px) rotateX(10deg)
}
100%{transform:perspective(400px) rotateX(0deg);opacity:1.0
}
}



