@charset "utf-8";

@font-face {
    font-family: '腾祥小新体繁';

    src:url('./fonts/腾祥小新体繁.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;

}

@font-face {
    font-family: '汉仪全唐诗繁';

    src:url('./fonts/汉仪全唐诗繁.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;

}
/* CSS Document */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, font, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	font-weight: inherit;
	font-style: inherit;
	font-size: 100%;
	font-family: inherit;
	vertical-align: baseline;
}

body {
	background-image:
	radial-gradient(hsla(0, 0%, 87%, 0.31) 9px, transparent 10px),
	repeating-radial-gradient(hsla(0, 0%, 87%, 0.31) 0,
	hsla(0, 0%, 87%, 0.31) 4px, transparent 5px,
	transparent 20px, hsla(0, 0%, 87%, 0.31) 21px,
	hsla(0, 0%, 87%, 0.31) 25px, transparent 26px,
	transparent 50px);
	background-size: 30px 30px, 90px 90px;
	background-position: 0 0;
}

/*这个很重要，针对aside里面的图片设置*/
img,video { 
	max-width: 100%; width: auto; 
}

/*此处是头部和导航部分*/
header {
	/*background-position: 0 top;
	background-repeat: repeat-x;
	background-image: url(./img/buntingFW.png);*/
	background: repeating-radial-gradient(2px 2px, ellipse,
	hsla(0,0%,100%,1) 2px, hsla(0,0%,95%,1) 10px,
	hsla(0,0%,93%,1) 15px, hsla(0,0%,100%,1) 20px);
	margin-right: auto; /* 10 ÷ 960 */
	margin-left: auto; /* 10 ÷ 960 */
	width: 97.9166667%; /* 940 ÷ 960 */
}

nav { 
	display: table; 
	margin-bottom: 25px; 
	margin-top: 26px; 
	padding-right: 1.0416667%;  
	padding-left: 1.0416667%; 
	width: 97.9166667%; 
	background: linear-gradient(top, hsla(0,0%,100%,0.11) 0%,hsla(0,0%,94%,0.11) 10%,hsla(0,0%,84%,0.11) 22%,hsla(0,0%,62%,0.11) 41%,hsla(0,0%,38%,0.11) 61%,hsla(0,0%,22%,0.11) 76%,hsla(0,0%,10%,0.11) 88%,hsla(0,0%,1%,0.11) 100%);
	border-bottom-color: #bfbfbf; 
	border-bottom-style: double; border-bottom-width: 4px; 
}

nav ul {
	display: table-row;
	width: 100%;
}

nav ul li { 
	text-align: center; 
	display: table-cell;
	margin-left: 1.3%;
	margin-right: 1.3%;
	transition: all .2s cubic-bezier(.4,0,1,1);
}

nav ul li a { 
	height: 42px;
	text-decoration: none; 
	text-transform: uppercase;  
	color: black; 
	text-shadow: 0 1px 0 hsla(0, 0%, 100%, 1.0);
	font: 1.875em/42px '腾祥小新体繁';
	display: block;	
	z-index:2;
}

nav ul li a:hover {
	-webkit-animation: warning 1.5s infinite ease-in;
}

nav ul li:last-child {
	text-align: right;
}
nav ul li:first-child {
	text-align: left;
}

/*此处是侧边栏部分*/
aside {
	border-right-color: #e8e8e8;
	border-right-style: solid;
	border-right-width: 2px;
	margin-top: 28px;
	padding-left: 1.5%; 
	padding-right: 1.0416667%; 
	margin-left: 1.0416667%; 
	float: right;
	width: 20.7083333%; 
	background: linear-gradient(90deg, #fff 0%, #e4e4e4 48.7%, #fff 100%);
}

aside h1 { 
	margin-bottom: 20px; 
	font-family: '汉仪全唐诗繁'; 
	font-size: 1.875em; /* 30px ÷ 16 */ 
	text-transform: uppercase; 
}

.overHyped { 
	margin-top: 27px; 
}
.sideBlock {
	width: 100%; /* 220 */
}
.moreInfo {
	padding: 5px 0px 5px 0px;
	color: #5c5959;
	text-shadow: 0px 1px 0px #fff;
	display: block;
	text-align: center;
	margin-bottom: 20px;
	margin-top: 10px;
	font-size: 1em;
	border-radius: 8px;
	border: 1px solid #999;
	background: linear-gradient(top, hsla(0,0%,1%,0.11) 0%,hsla(0,0%,10%,0.11) 12%,hsla(0,0%,22%,0.11) 24%,hsla(0,0%,38%,0.11) 39%,hsla(0,0%,62%,0.11) 59%,hsla(0,0%,84%,0.11) 78%,hsla(0,0%,94%,0.11) 90%,hsla(0,0%,100%,0.11) 100%);
	transition: all 0.7s;
}

.moreInfo:nth-child(2) {
	margin-left: 3%;
}
.moreInfo:hover {
	cursor: pointer;
	background: linear-gradient(top, hsla(0,0%,100%,0.11) 0%,hsla(0,0%,94%,0.11) 10%,hsla(0,0%,84%,0.11) 22%,hsla(0,0%,62%,0.11) 41%,hsla(0,0%,38%,0.11) 61%,hsla(0,0%,22%,0.11) 76%,hsla(0,0%,10%,0.11) 88%,hsla(0,0%,1%,0.11) 100%);
	box-shadow: 0px 3px 4px #444;
}

.sideBlock a {
	max-width: 47%;
	text-decoration: none;
	float: left;
}
.sideBlock img {
	box-shadow: 0px 3px 5px #444;
	margin-bottom: 5px;
}

.sideImage {
	margin-left: 3%;
}

.unSung a:nth-child(odd) img {
	transform: rotate(3deg);
	animation: swing 0.1s 5 ease-in;
}
.unSung a:nth-child(even) img {
	transform: rotate(-3deg);
	animation: swing 0.1s 5 0.3s ease-in;
}
.overHyped a:nth-child(odd) img {
	transform: rotate(3deg);
	animation: swing 0.1s 5 0.2s ease-in;
}
.overHyped a:nth-child(even) img {
	transform: rotate(-3deg);
	animation: swing 0.1s 5 0.5s ease-in;
}

/*以下内容是主体内容*/
#content {
	margin-top: 28px;
	margin-right: 1.0416667%; /* 10 ÷ 960 */
	float: left;
	width: 72.7083333%; /* 698 ÷ 960 */	
	height: 100%;
}

.oscarMain { 
	float: left; 
	width: 28.9398281%; /* 698 ÷ 202 */
	max-width: 202px;
	margin-right: 1%;
}

#content h1 { 
	text-transform: uppercase; 
	font-family: '汉仪全唐诗繁';
	font-size: 6.375em;  /* 102 ÷ 16 */
	text-shadow: 0px 1px white,4px 4px 0px #dad7d7 ; /* 4 ÷,4px 4px 0px #dad7d7 102 */
	font-weight: 400;
}

#content h1 em {
	display: block; 
	color: #504e4e; 
	text-shadow: none;
	font: .352941176em/1.052631579em '汉仪全唐诗繁';
	font-weight: 400;
}

#content h2 {
	font-family: '汉仪全唐诗繁'; 
	font-size: 1.875em; /* 30px ÷ 16 */ 
	text-transform: uppercase;
	font-weight: 400;
}

#content h2 a {
	color: #FD0105;
}

#content p { 
	font-family: '汉仪全唐诗繁';
	line-height: 40px; 
	color: #757474; 
	font-size: 1.875em; /* 24px ÷ 16 */ 
}
#content p em {
	text-decoration: underline;
}

#content a.button {
	text-decoration: none;
	font: 2.25em '汉仪全唐诗繁'; /* 36px ÷ 16 */
	background-color: #b01c20;
	background: linear-gradient(top, rgb(241,92,96) 0%, rgb(176,28,32) 100%);
	border-radius: 8px;
	color: white;
	padding: 3%;
	float: left;
	margin-top: 30px;
	box-shadow: 5px 5px 5px hsla(0, 0%, 26.6667%, 0.8);
	text-shadow: 0px 1px black;
	border: 1px solid #bfbfbf;
	transition-property: border, color, text-shadow;
	transition-duration: 2s, 3s, 8s; 
}
#content a.button:hover {
	border: 1px solid black;
	color: black;
	text-shadow: 0px 1px white;
	background: linear-gradient(top, rgb(145,62,56) 0%,rgb(176,28,32) 100%);	
}

@keyframes warning {
	0% {
		text-shadow: 0px 0px 4px #000000;
	}
	50% {
		text-shadow: 0 0 40px #000000;
	}
	100% {
		text-shadow: 0 0 4px #000000;
	}
}








