
.section-title {
	padding:50px 0;
	font-family:"Gill Sans Alt One WGL W01 Lt",Calibri,sans-serif;
	max-width:600px;
	margin-left:auto;
	margin-right:auto;
	font-size:48px;
	font-weight:400;
	line-height:100%;
	text-align:center;
}
#categories-container {
	height:100px;
	//width:1313px;
	width:100%;

	position:relative;
	top:620px;
	z-index:-1;
}
.type3 {
	padding:0 20px;
	position:absolute;
	top:850px;
	z-index:-3;
	left:70px;
}
div .image:hover img {
	transform:scale(1.1);
	-webkit-transform:scale(1.1);
}
.image img {
	-webkit-transition:all 1s ease;
	-moz-transition:all 1s ease;
	-ms-transition:all 1s ease;
	-o-transition:all 1s ease;
	transition:all 1s ease;
}
div .module{
	position:relative;
	//height:650px!important;
	height:40%;
	//width:387px;
	width:30%;
	float:left;
}
.title_ca {
	color:#348faa;
}
.title_ca h3 {
	margin-bottom:20px;
	font-size:28px;
	font-family:"Gill Sans Alt One WGL W01 Rg",Calibri,sans-serif;
	font-weight:400;
}
.read-more {
	position:relative;
	color:#11809F;
}
.read-more:hover {
	color:#D77B00;
}
div .image {
	position:relative;
	//height:350px!important;
	height:50%;
	//width:387px;
	width:100%;
	border:0;
	overflow:hidden;
	max-width:100%;
	height:auto;
	float:left;
}
div .image img{
	//height:350px!important;
	//width:387px;
	height:50%;
	//width:387px;
	width:100%;
}
a:link {
 text-decoration: none;
}
.content_ca{
	background-color:#e6eaeb;
	padding:30px;
	height:580px;
}
.content_ca p{
	color:#969696;
	font:'腾祥小新体繁';
}
/***********************************************************/
.career {
	position:absolute;
	width:100%;
	height:100%;
}
.left{
	background-image: url(../assets/img/enter_culture.jpg);
	background-position-x: left;
    background-position-y: 0px;
	background-size:cover;
	height:100%;
	width:50%;
	float:left;
	overflow:hidden;
	position:absolute;
	left:0;
	transition:width 1.5s ease;
	display:block;
}
.inner{
	width:100%;
	height:100%;
	position:absolute;
	top:35%;
	text-align:center;
	font: 45px '汉仪全唐诗繁';
	//font-size:45px;
	color:white;
}
.right{
	height:100%;
	background-image: url(../assets/img/enter_impact.jpg);
	background-position-x: right;
    background-position-y: 0px;
	background-size:cover;
	height:100%;
	width:50%;
	float:right;
	overflow:hidden;
	position:absolute;
	right:0;
	display:block;
	transition:width 1.5s ease;
}
.h_left,.h_right {
	width:67%;
}
.nh_left,.nh_right {
	width:34%;
}
.section_ca {
	width:100%;
	height:580px;
	//height:70%;
	position:absolute;
}



