	//left: 1024px;
	left:100%;
	color: white;
	//width:100%;
}
.section1 .title_sch{
	color: #FF8500;
}
.section2 .title_sch{
	color: #FF8500;
}
.section3 .title_sch{
	opacity: 0;
	//top: 360px;
	top:20%;
	color: #FF8500;
}
.title-anim{
	//top: 360px;
	top:20%;
	left:7%;
	-webkit-transition : all 1s;
	-moz-transition : all 1s;
	-ms-transition : all 1s;
	transition : all 1s;
}
.title-anim2{
	opacity: 1!important;
	left:7%;
	-webkit-transition : all 2s;
	-moz-transition : all 2s;
	-ms-transition : all 2s;
	transition : all 2s;
}
.content-anim{
	//left: 0px;
	left:7%;
	-webkit-transition : all 1s;
	-moz-transition : all 1s;
	-ms-transition : all 1s;
	transition : all 1s;
}
